export default{
	data(){
		return {
			offset: 0,
			onTheTop:true,
			touchSpot: {
				startX: 0,
				startY: 0
			 },
			 pulldownTop:0
		}
	},
	methods:{
		bindscrolltolower: function (e) {
			console.log(1)
		      if (!this.data.more || this.lock) return;
		      this.lock = true;
		      // this.triggerEvent('reachbottom', {3	
		      //   stop: () => {
		      //     this.lock = false;
		      //   }
		      // }, {});
		    },
		    bindscrolltoupper: function (e) {
		     console.log(2)
		      this.onTheTop = true;
		    },
		    bindscroll: function (e) {
				console.log(3)
		      // console.log(e.detail.scrollTop)
		      if (e.detail.scrollTop === 0) {
		        this.onTheTop = true;
		      } else {
		        this.onTheTop = false;
		      }
		    },
		    touchstart(e) {
				console.log(4)
		      if (!this.onTheTop || !e.changedTouches[0]) return;
		      const { pageX, pageY } = e.changedTouches[0];
		      this.touchSpot.startX = pageX;
		      this.touchSpot.startY = pageY;
		    },
		    touchmove(e) {
				
		      //return//关闭下拉刷新
		      if (!this.onTheTop || !e.changedTouches[0]) {
		        return;
		      } else if (!this.touchSpot.startX) {
		        const { pageX, pageY } = e.changedTouches[0];
		        this.touchSpot.startX = pageX;
		        this.touchSpot.startY = pageY;
				
		        return;
		      }
		      const { pageX, pageY } = e.changedTouches[0];
		      let offset = (pageY - this.touchSpot.startY) / 2;
			  this.offset=offset > 100 ? 100 : offset;
		      // this.setData({
		      //   offset: offset > 100 ? 100 : offset
		      // });
		    },
		    touchend(e) {
		      // return
		      if (this.offset >= 70) {
		        if (this.offset < 100) {
					this.offset=100;
		          // this.setData({
		          //   offset: 100
		          // });
		        }
		        // 下拉刷新
		        // this.triggerEvent('pulldown', {
		        //   stop: () => {
		        //     setTimeout(() => {
		        //       this.setData({
		        //         offset: 0
		        //       });
		        //     }, 600);
		        //   }
		        // }, {});
		      } else {
				  this.offset=0
		        // this.setData({
		        //   offset: 0
		        // });
		      }
		      this.resetTouchSpot();
		    },
		    resetTouchSpot() {
		      this.touchSpot = {
		        startX: 0,
		        startY: 0
		      };
		    }
	}
}
